<template>
    <div class="appBox">
        <div class="header">
            <img src="@/static/images/modify/redbg.png" alt />
        </div>
        <img class="tiao" src="@/static/images/modify/tiao_image.png" alt="">
        <div class="activeRule">
            <div class="title"><i class="leftImages"></i>像刷抖音一样抢红包！<i class="rightImages"></i></div>
            <p>红包接龙玩法，永远抢不完的红包等你来拿！创建红包接龙群，群主可以拿房间内流水的3%，每天只要有33个红包，您将会有一倍的收益！</p>
        </div>
        <img class="sheng1" src="@/static/images/modify/shengjie_image.png" alt="">
        <img class="sheng2" src="@/static/images/modify/shengjie_image.png" alt="">
        <div class="activeRule">
            <div class="title"><i class="leftImages"></i>用户福利<i class="rightImages"></i></div>
            <p>首次注册就送100元的MCT平台币！创建红包群再送100元的MCT平台币！注：MCT是millioncoin.name的平台币，未来MCT将会在millioncoin.name平台中起到空投糖果所得权、平台股份的持币分红权等应用，millioncoin.name也将给MCT更多赋能，让MCT有更多的价值落地。</p>
        </div>
        <img class="sheng1" src="@/static/images/modify/shengjie_image.png" alt="">
        <img class="sheng2" src="@/static/images/modify/shengjie_image.png" alt="">
        <div class="main ov">
            <div class="fl ovf">
                <p class="pwd1">注册邀请码</p>
                <p class="code">{{inviteInfo.invite_code}}</p>
                <button class="copy" @click="copyInvite(inviteInfo.invite_code)">复制</button>
            </div>
            <div class="fr ovf">
                <p class="pwd1">房间密码</p>
                <p class="code">{{inviteInfo.room_number}}</p>
                <button class="copy" @click="copyInvite(inviteInfo.room_number)">复制</button>
            </div>
            <img :src="inviteInfo.qrcode_url" alt />
            <p class="bottom">扫描二维码，马上注册抢红包！</p>
        </div>
    </div>
</template>

<script>
    import { shareInfo } from '@/api/red'
    import Clipboard from 'clipboard'
    import Toast from 'vant/lib/toast';
    import 'vant/lib/toast/style';
    export default {
        name: "Redshare",
        data()
        {
            return {

                inviteInfo:{
                    invite_code:'',
                    room_number:'',
                    qrcode_url:''
                }
            }
        },


        created()
        {

            this.redgroup_id = this.$route.query.groupId
            this.getInviteInfo()
        },
        mounted()
        {

        },
        methods:{
            // 获取邀请信息
            getInviteInfo()
            {
                shareInfo({redgroup_id:this.redgroup_id}).then(res => {
                    this.inviteInfo = res
                })
            },
            // 复制邀请码
            copyInvite(copyText)
            {

                var clipboard = new Clipboard('.copy', {
                    text: function() {
                        return copyText;
                    }
                });
                clipboard.on('success', e => {

                    Toast.success('复制成功');
                    // 释放内存
                    clipboard.destroy()
                })
                clipboard.on('error', e => {
                    Toast.fail('当前浏览器不支持');
                    // 释放内存
                    clipboard.destroy()
                })

            }
        }
    }
</script>

<style scoped>
     @import "../../static/css/redshare.css";
</style>



